ul,
li {
	padding: 0;
	margin: 0;
	list-style: none;
}

.sfc {
	display: flex;
	justify-content: center;
	align-items: center;
}

.sfv {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.sf {
	display: flex;
}

.speed-dark {
	background: #000 !important;
	color: #fff !important;
}
